@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}
body {
  /* color: aliceblue; */
  /* overflow: hidden; */
  z-index: -999;
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@keyframes appear {
  0% {
    clip-path: circle(30% at -25% -25%);
  }
  100% {
    clip-path: circle(150% at 0 0);
  }
}

.main-body-dark{
  transition: all ease-in-out 1s;
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  min-height: 100%;
  min-width: 100%;
  background: url(./autobot.jpg) no-repeat center center fixed;
  color: aliceblue;
  transition: all ease-in-out 1s;

}
.main-body-light{
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  min-height: 100%;
  min-width: 100%;
  background: url(./lit2.png) no-repeat center center fixed;
  color: rgb(0, 0, 0);
  transition: all ease-in-out 1s;

}
.main-body-light section{
  mix-blend-mode: normal;
  display: none;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home-container {
  animation: floatery 1s;
  backdrop-filter: blur(7px);
  margin-top: 3%;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  height: fit-content;
  padding: 3%;
  border-radius: 12px;
  border: 2px solid white;
  background-color: rgba(0, 0, 0, 0.242);
  box-shadow: 10px 25px 30px rgba(0, 136, 134, 0.384);
}
.home-container p {
  /* color: aliceblue; */
  text-align: justify;
}

.alert{
  z-index: 999;
}


.toastmessage{
  z-index: 999;
  border: 2px solid white;
  display: block;
  width: 100%;
}